.game-area {
  position: relative;
  margin: 50px auto;
  width: 600px;
  height: 600px;
  border: 2px solid #000000;
}

.snake {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #068016;
  border: 1px solid white;
  z-index: 2;
}

.snake-food {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #ff0000;
  border: 1px solid white;
  border-radius: 50%;
  z-index: 1;
}